@import"https://use.fontawesome.com/releases/v5.13.0/css/all.css";.progress{position:relative;display:flex;width:70%;margin:0 auto;transition:width 1s ease-in;color:#000}.progress-track{transition:width 1s ease-in;position:absolute;top:5px;width:100%;height:5px;background-color:#dfe3e4;z-index:-1}.progress-step{position:relative;width:100%;font-size:12px;text-align:center}.progress-step:last-child:after{display:none}.progress-step:before{font-weight:900;content:"";display:flex;margin:0 auto;margin-bottom:10px;width:10px;height:10px;background:#fff;border:4px solid #dfe3e4;border-radius:100%;color:#fff}.progress-step:after{content:"";position:absolute;top:6px;left:50%;width:0%;height:5px;background:#dfe3e4;z-index:-1}.progress-step.is-active{color:#2183dd}.progress-step.is-active:before{border:4px solid #777;animation:pulse 2s infinite}.progress-step.is-complete{color:#090}.progress-step.is-complete:before{font-family:"Font Awesome 5 Free";font-size:10px;color:#fff;background:#090;border:4px solid transparent}.progress-step.is-complete:after{background:#2183dd;animation:nextStep 1s;animation-fill-mode:forwards}.progress-step.is-not-complete{color:#2183dd}.progress-step.is-not-complete:after{background:#2183dd;animation:prevStep 1s;animation-fill-mode:forwards}@keyframes pulse{0%{box-shadow:0 0 0 0 rgba(33,131,221,.4)}70%{box-shadow:0 0 0 10px rgba(33,131,221,0)}100%{box-shadow:0 0 0 0 rgba(33,131,221,0)}}@keyframes nextStep{0%{width:0%}100%{width:100%}}@keyframes prevStep{0%{width:100%}100%{width:0%}}