.TopicInformation{
    font-size: 2.5em;
    font-weight: bold;
    margin-bottom: 20px;
  }

  label{
    font-weight: bold;
  }

  .GroupForm{
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 10px;
  }
  
  .GroupForm2{
    width: 100%;
    display: flex;
    margin-top: 10px;
  }
  
  .GroupFormRight{
    padding-left: 10px;
    width: 50%;
    display: flex;
    flex-direction: column;
  }
  
  .GroupFormLeft{
    width: 45%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .TemplateBox{
    margin-top: 10px;
    width: 100%;
    max-height: 33rem;
    padding:10px 0px;
    position: relative;
  }

  .TemplateBox .Box{
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow:hidden;
    overflow-y : scroll; 
  }

  .TemplateBox .Box::-webkit-scrollbar {
    display: none;
}

  .RowTemplate{
    display: flex;
  }

  .ImageTemplate{
    width: 50%;
    padding:5px;
  }

  .ImageTemplate img{
    width: 100%;
    height:11rem;
    border-radius: 10px;
    cursor: pointer;
    transition: transform .2s;
  }

  .ImageTemplate img:hover{
    transform: scale(1.1);
  }

 