.SummaryWrapper h1 {
  font-size: 3em;
  color: #5e5de4;
  font-weight: bold;
}

.SummaryWrapper p {
  font-size: 2em;
}

.SummaryWrapper .total {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 20px;
}

.SummaryWrapper .total .nominal {
  width: 44%;
  display: flex;
  justify-content: flex-end;
}

.salin {
  display: flex;
  justify-content: flex-end;
}

.salin a {
  width: 15%;
}

.PayWith {
  margin: 10px 0px;
  font-weight: bold;
  font-size: 1.5em !important;
}

.button {
  width: 100%;
  display: flex;
  justify-content: flex-end;

  p {
    color: red;
    font-size: 1.5rem;
    align-self: flex-end;
    margin-right: auto;
  }
}

.button button {
  padding: 10px 50px;
  border: none;
  border-radius: 50px;
  margin-top: 30px;
  font-size: 1.5em;
  text-decoration: none !important;
  background-color: #5e5de4;
  color: white !important;
}

.PaymentMethod {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px 0px;
}

.PaymentMethod select {
  width: 95%;
  padding: 5px;
  border-radius: 50px;
  font-size: 15px;
  outline: none;
  cursor: pointer;
}

.PaymentMethod .Rekening {
  font-size: 2.5em;
  font-weight: bold;
  margin-top: 20px;
}

.PaymentMethod a {
  font-size: 1.2em;
  margin-top: 5px;
}

.PaymentMethod .Ewallet {
  width: 100%;
  font-size: 1.8em;
  font-weight: bold;
  margin-top: 20px;
  text-align: left;
}

.PaymentMethod input {
  padding: 10px 0px;
  width: 100%;
  margin-top: 5px;
  border: none;
  outline: none;
  border-bottom: 2px solid #bcbcbc;
  font-size: 1.5em;
}

.PaymentMethod input:focus {
  border-bottom: 2px solid #5e5de4;
}

.CreditCard {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.CreditCard .GroupForm {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: flex-start;
  margin-bottom: 10px;
}

label {
  font-size: 1.5em;
  font-weight: bold;
}

.GroupForm input {
  padding: 10px 0px;
  width: 100%;
  margin-top: 5px;
  border: none;
  outline: none;
  border-bottom: 2px solid #bcbcbc;
  font-size: 1.5em;
}

.GroupForm input:focus {
  border-bottom: 2px solid #5e5de4;
}

.GroupForm2 input {
  padding: 10px 0px;
  width: 100%;
  margin-top: 5px;
  border: none;
  outline: none;
  border-bottom: 2px solid #bcbcbc;
  font-size: 1.5em;
}

.GroupForm2 {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.HalfForm {
  width: 48%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
